import { UserService } from '../../../shared/userService/user.service';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { ToasterService } from '../../../shared/toastrService/toaster.service';
import { AfterViewInit, Component, ViewChild, OnInit , OnDestroy } from '@angular/core';
import { environment } from '../../../../environments/environment';
@Component({
  selector: 'app-top-navbar',
  templateUrl: './top-navbar.component.html',
  styleUrls: ['./top-navbar.component.css']
})
export class TopNavbarComponent implements OnInit {
  user;
  formGroup: FormGroup;
  formData = new FormData();
  interval;
  constructor(
    private userService: UserService,
    private router : Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
  ) { }

  ngOnInit(): void {
    this.user=this.userService.getToken()['userInfo'];
    this.refresh();
    this.checkLogin()
  }
  checkLogin(){
    const checkIfLogin = ()=>{
      if(this.userService.isLoggedIn()==false) this.router.navigateByUrl(this.userService.loginURL);
    }
   this.interval = setInterval(checkIfLogin,5000);
  }
  ngOnDestroy(){
    clearInterval(this.interval);
  }
  cancel(){
    this.refresh();
  }
  save(){
        this.userService.postRequest('api/editPassword',{
          user:this.user,
          data:this.formGroup.value
        }).subscribe(
          res=>{
            this.Toaster('success','','success');
            this.refresh();
          },
          err=>{
            this.refresh();
            this.handleError(err)
          }
        );
  }
  refresh(){
    const accountRegex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z0-9]{11}$/;
    const intenationalPhone = /^1[3|4|5|7|8][0-9]{9}$/;
    const dateRegex=/^\d{4}-\d{2}-\d{2}$/;
    this.formGroup = this._formBuilder.group({
      username:[this.user.username,Validators.required],
      previous:['',Validators.required],
      password:['',Validators.required],
      confirm:[''],
    });
  }
  passwordConfirm(){
    if(this.formGroup.value.password.length>0&&(this.formGroup.value.password!=this.formGroup.value.confirm))
    return false
    else if(this.formGroup.value.password.length>0&&(this.formGroup.value.password==this.formGroup.value.confirm)) return true
    else return true
  }
  logOut(){
    this.userService.deleteToken();
    this.userService.gotoFirstPage();
  }
  getAvatar(){
    return this.user.avatar;
  }
  getName(){
    if('nickname' in this.user) return this.user.nickname;
    else return this.user.company;
  }
  //toastr functions
  handleError(err){
    if(err.status==504)  this.Toaster('error', '', 'server is not responsing.',4000);
    else         this.Toaster('error', '', err.error.message,1500);
   }
  Toaster(style,msgtopic,msgContent,time=1000) {
    this.toastr.show(style, msgtopic, msgContent, time);
  }
}
