import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './shared/userService/auth/auth.guard';
import { HomeComponent } from './components/pages/home/home.component';
import { DefaultLoginComponent } from './components/pages/default-login/default-login.component';
import { ErrorComponent } from './components/pages/error/error.component';
const routes: Routes = [
{path: '', redirectTo:'/home', pathMatch:'full'},
{path: 'home', component: HomeComponent},
{path: 'home/:id', component: HomeComponent},
{path: 'login', component: DefaultLoginComponent},
{
  path: 'admin',canActivate:[AuthGuard],
  loadChildren:
    () => import('./components/admin_system/system.module').then(m => m.SystemModule)
},
{
  path: 'company',canActivate:[AuthGuard],
  loadChildren:
    () => import('./components/admin_company/company.module').then(m => m.CompanyModule)
},
{
  path: 'map',canActivate:[AuthGuard],
  loadChildren:
    () => import('./components/map/map.module').then(m => m.MapModule)
},
{path: '**',component: ErrorComponent},
];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
