import { AuthInterceptor } from './auth/auth.interceptor';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { Router } from "@angular/router";

@Injectable({
  providedIn: 'root'
})
export class UserService {
  loginURL = '/login';
  noAuthHeader = { headers: new HttpHeaders({ 'NoAuth': 'True' }) };

  constructor(
    private http: HttpClient,
    private router: Router,
  ) { }
  //for preview landing page setting
  setLocalSetting(data) {
    localStorage.setItem('data', JSON.stringify(data));
  }

  getLocalSetting() {
    return JSON.parse(localStorage.getItem('data'));
  }
  deleteLocalSetting() {
    localStorage.removeItem('data');
  }
  //For login & register
  postRequest(URL, data = {}, header = true) {
    if (header) return this.http.post(URL, data, this.getHeader())
    else return this.http.post(URL, data, this.noAuthHeader)
  }
  gotoFirstPage() {
    var token = this.getToken();
    if (token) {
      const role = this.getToken().userInfo.role;
      if (role == 'super' || role == 'system') this.router.navigateByUrl('admin')
      else if (role == 'admin') this.router.navigateByUrl('company')
      else this.router.navigateByUrl('map')
    } else {
      this.router.navigateByUrl('')
    }
  }
  setToken(token) {
    localStorage.setItem('token', JSON.stringify(token));
  }

  getToken() {
    return JSON.parse(localStorage.getItem('token'));
  }

  deleteToken() {
    localStorage.removeItem('token');
  }

  getUserPayload() {
    var token = this.getToken();
    if (token) {
      var userPayload = atob(token['token'].split('.')[1]);
      return JSON.parse(userPayload);
    }
    else
      return null;
  }

  isLoggedIn(data: { role: [] } | any = {}) {
    var userPayload = this.getUserPayload();
    if (userPayload) {
      const role = this.getToken().userInfo.role;
      if (data.role == undefined) return userPayload.exp > Date.now() / 1000;
      else if (data.role.indexOf(role) != -1) return userPayload.exp > Date.now() / 1000;
    }
    else return false;
  }
  getHeader() {
    if(!this.getToken()) {
      this.router.navigateByUrl(this.loginURL);
      return;
    }
    else if(this.isLoggedIn()){
      this.router.navigateByUrl(this.loginURL);
      return;
    }
    const header = { headers: { Authorization: `Bearer ${this.getToken().token}` }, user: this.getToken().userInfo }
    return header;
  }
}
