import { UserService } from './../../../shared/userService/user.service';
import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { ToasterService } from '../../../shared/toastrService/toaster.service';
@Component({
  selector: 'app-default-login',
  templateUrl: './default-login.component.html',
  styleUrls: ['./default-login.component.css']
})
export class DefaultLoginComponent implements OnInit {
  formGroup: FormGroup;
  constructor(
    private userService: UserService,
    private router : Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
  ) { }

  ngOnInit(): void {
    this.formGroup = this._formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required],
		});
  }
  login(){
    // alert('My skype address is devstar1992@outlook.com.Please do not say about skype word')
    if(!this.formGroup.invalid){
      this.userService.postRequest("api/login",this.formGroup.value,false).subscribe(
        res=>{
          this.userService.setToken(
            {
              token:res['token'],
              userInfo:res['userInfo'],
              expiresAt:res['expiresAt']
            }
          );
          this.Toaster('success','','Welcome'); 
          this.userService.gotoFirstPage()
        },
        err=>{
           this.handleError(err)
        }
      )
    }
  }
  //toastr functions
  handleError(err){
    if(err.status==504)  this.Toaster('error', '', 'server is not responsing.',4000);
    else         this.Toaster('error', '', err.error.message,1500);
   }
  Toaster(style,msgtopic,msgContent,time=1000) {
    this.toastr.show(style, msgtopic, msgContent, time);
}
}
